import logo from '../resources/ChainTrackerLogo.PNG'
import './MainStyle.css'
import React from 'react'
import { Link } from 'react-router-dom'
import { useAuth } from '../contexts/AuthContext'
import Button from 'react-bootstrap/Button'

//Determines whether or not the user is logged in
export default function App(){
  const { currentUser } = useAuth();
  return (
    <div className = "body">
      {currentUser ?  <LoggedIn /> : <NotLoggedIn />} 
    </div>
  );
}
//Display when user is logged in
function LoggedIn(){
  return (
    <div className="body">
      <img src = {logo} alt = "Logo"/>
      <h1>Chain Tracker</h1>
      <h2>Reliable and secure.</h2> 
      <p>Store data using blockchain.</p>
      <Link to = "/Software">
        <Button>
          Software
        </Button>
      </Link>
    </div>
  );
}
//Display when user isn't logged in
function NotLoggedIn(){
  return (
    <div className="body">
      <img src = {logo} alt = "Logo"/>
      <h1>Chain Tracker</h1>
      <h2>Reliable and secure.</h2> 
      <p>Store data using blockchain.</p>
      <Link to = "/Account/SignUp">
        <Button>
          Login or Sign Up
        </Button>
      </Link>
    </div>
  );
}