import { React, useState, useRef } from 'react'
import axios from 'axios'
import Form from 'react-bootstrap/Form'
import Button from 'react-bootstrap/Button'
import Card from 'react-bootstrap/Card'
import ListGroup from 'react-bootstrap/ListGroup'
import ListGroupItem from 'react-bootstrap/ListGroupItem'
import Alert from 'react-bootstrap/Alert'
import Spinner from 'react-bootstrap/Spinner'
import { useAuth } from '../contexts/AuthContext'
import { Link } from "react-router-dom"

//Check to see if the user has logged in or not
export default function Software(){
  const { currentUser } = useAuth()
  return (
    <div>
      {currentUser ? <LoggedIn /> : <NotLoggedIn  />}
    </div>
  );
}
//Display if user is not logged in
function NotLoggedIn(){
  return (
    <div className = "center">
      <div className = "body">
        <h1>Record Transactions</h1>
        <p>Submit files to the blockchain and recieve transaction receipts.</p>
        <Link to = {"/Account/SignUp"}>
          <Button>
            Login or Sign Up
          </Button>
        </Link>
      </div>
    </div>
  );
}
//If you are logged in you have access  to upload a file to Blockchain
function LoggedIn(){
  const [ selectedFile, setSelectedFile ] = useState(null)
  const fileRef = useRef()
  const { currentUser } = useAuth();
  const [ fileSent, setFileSent ] = useState(false)
  const [ loading, setLoading ] = useState(false);

  function fileSubmit(e){
    e.preventDefault()
    // console.log("hi")

    setLoading(true);
    const getBase64 = file => {
      return new Promise(resolve => {
        let baseURL = "";
        // Make new FileReader
        let reader = new FileReader();
        
        // Convert the file to base64 text
        reader.readAsDataURL(file);
    
        // on reader load somthing...
        reader.onload = () => {
          // Make a fileInfo Object
          baseURL = reader.result;
          resolve(baseURL);
        };
      });
    };

    getBase64(selectedFile)
      .then(result => {
        //location to where the file will be stored
        axios.post('http://0.0.0.0:5000/'+currentUser.uid, {hash: result.substring(0)})
        .then(res => {
          console.log(res);
          setLoading(false);
          setSelectedFile(null)
          setFileSent(true)
          setTimeout(() => setFileSent(false), 7000)
        })
      })
      .catch(err => {
      console.log(err);
    });
  }
    
  //Finds the file that was selected
  function fileUploaded(e){
    e.preventDefault()
    const files = Array.from(e.target.files)
    setSelectedFile(files[0])
  }

  return (
    <div className = "center">
      <div className = "body">
        {fileSent && <div className = "body"><Alert variant = "success"> Your file was sent to the blockchain!</Alert></div>}
        <Card>
          <Card.Header as = "h3">Hi, {currentUser.displayName}</Card.Header>
            <Card.Body>
              <Card.Title>Send information to the blockchain here.</Card.Title>
                <ListGroup className = "list-group-flush">
                  <ListGroupItem>
                    <Form onSubmit = {fileSubmit} onChange = {fileUploaded}>
                      <div className = "mb-3">
                        <Form.File id = "formcheck-api-custom" custom ref = {fileRef}>
                        {selectedFile ? <Form.File.Input isValid /> : <Form.File.Input isInvalid />}
                        <Form.File.Label data-browse = "Choose file">
                        {selectedFile ? selectedFile.name : "Click Here to Select a File"}
                        </Form.File.Label>
                        {selectedFile ? <Form.Control.Feedback type = "valid">File was successfully chosen!</Form.Control.Feedback> : <Form.Control.Feedback type = "invalid">Choose a File to Send to the Blockchain</Form.Control.Feedback> }
                        <div className = "body">
                        <br/>
                        {!loading ? <Button variant = "primary" type = "submit">
                          Send to Blockchain
                          </Button> : 
                          <Button variant = "primary" disabled>
                          <Spinner
                            as = "span"
                            animation = "grow"
                            size = "sm"
                            role = "status"
                            aria-hidden = "true"
                          />
                          {'Sending...'}
                          </Button>
                        }
                      </div>
                    </Form.File>
                  </div>
                </Form>
              </ListGroupItem>
            </ListGroup>
          </Card.Body>
        </Card>
      </div>
    </div>
  );
}