import React from 'react'
import './MainStyle.css'
import Card from 'react-bootstrap/Card'
import Row from 'react-bootstrap/Row'
import Container from 'react-bootstrap/Container'

export default function Contacts(){
	//Component function that passes props to create contact boxes
	function Box(props){
		return (
			<Container style = {{display: 'flex', alignItems: 'baseline', justifyContent:'center', flex: '2'}}>
				<div className = "contact">
					<Card>
					<Card.Body>
						<Card.Title>{props.name}</Card.Title>
						<Card.Subtitle className = "mb-2 text-muted">{props.title}</Card.Subtitle>
						<Card.Text>{props.email}</Card.Text>
						<Card.Text>{props.github} on GitHub</Card.Text>
					</Card.Body>
					</Card>
				</div>
			</Container>
		);
	}
	//Displays title/description and initializes props
	function Team(){
		return (
			<div className = "center">
				<div className = "body">
					<h1>Meet the Team</h1>
					<p>Chain Tracker development is led by a dedicated team working at Lunatic Labs. One of the two sub-teams implements the blockchain program, the other works on the Chain Tracker website and documentation.</p>
					<Row>
						<Box name = "Ian Reitmaier" title = "Founder and Blockchain Team Lead" email = "reitmaieir@mail.lipscomb.edu" github = "IanReit"/>
						<Box name = "Alexander Nordhoff" title = "Blockchain Team" email = "aenordhoff@mail.lipscomb.edu" github = "alexnordhoff"/>
					</Row>
					<Row>
						<Box name = "Nathan Matheis" title = "Blockchain Team" email = "ntmatheis@mail.lipscomb.edu" github = "nmath315"/>
						<Box name = "Bryce Martin" title = "Blockchain Team" email = "bjmartin@mail.lipscomb.edu" github = "Brycejax"/>
					</Row>
					<Row>
						<Box name = "Iyobed Zekarias" title = "Blockchain Team, Website Team Lead" email = "zekariasi@mail.lipscomb.edu" github = "IyobedZekarias"/>
						<Box name = "Shelby Rutland" title = "Website Team" email = "snrutland@mail.lipscomb.edu" github = "shellrut"/>
					</Row>
					<Row>
						<Box name = "Samuel Glynn" title = "Website Team" email = "scglynn@mail.lipscomb.edu" github = "scglynn"/>
						<Box name = "Jeffrey Kendig" title = "Website Team" email = "jckendig@mail.lipscomb.edu" github = "jeffreykendig"/>
					</Row>
				</div>
			</div>
		);
	}
	return Team();
}