import './MainStyle.css'
import { React, useState, useRef } from 'react'
import Nav from 'react-bootstrap/Nav'
import Form from 'react-bootstrap/Form'
import Button from 'react-bootstrap/Button'
import Alert from 'react-bootstrap/Alert'
import Card from 'react-bootstrap/Card'
import ListGroupItem from 'react-bootstrap/ListGroupItem'
import ListGroup from 'react-bootstrap/ListGroup'
import {
    Switch,
    Route,
    Link,
    useParams,
    useRouteMatch, 
    useHistory
  } from "react-router-dom"
import { useAuth } from '../contexts/AuthContext'

//Account functions sets up that inner routes of the page for how users will be creating/logging into accounts
//Also sets up connection to firebase and sends information to ../contexts/AuthContext
export default function Account(){
  const { currentUser } = useAuth();
  return (
    <div className = "body">
      {currentUser ?  <LoggedIn /> : <NotLoggedIn />} 
    </div>
  );
}
//what to display if the user is logged in
//This also will call a button 
function LoggedIn(){
  const emailRef = useRef();
  const [ error, setError ] = useState('');
  const [ loading, setLoading ] = useState(false)
  const { currentUser } = useAuth();
  const { logout, updateEmail, forgot } = useAuth(); 
  const [ message, setMessage ] = useState('')

  async function handleLogout(e){
    e.preventDefault(); 
    try{
      setError("")
      setLoading(true)
      await logout()
    } catch { 
      setError("Logout Failed")
    }
      setLoading(false);
  }

  async function handleEmail(e){
    e.preventDefault();
    try{
      setError("")
      setLoading(true)
      await updateEmail(emailRef.current.value)
      emailRef.current.value = ""
      setMessage("Email Updated Successfully")
      setTimeout(()=>setMessage(''), 7000)
    } catch {
      setError("Could not update email")
      setTimeout(()=>setError(''), 7000)
    }
    setLoading(false)
  }

  async function handlePassword(e){
    e.preventDefault();
    try{
      setError("")
      setLoading(true)
      await forgot(currentUser.email)
      setMessage("and email has been sent to '" + currentUser.email + "' with password reset instructions")
      setTimeout(()=>setMessage(''), 7000)
    } catch {
      setError("Could not update password")
      setTimeout(()=>setError(''), 7000)
    }
      setLoading(false)
  }

  return (
    <div className = "center">
      <div className = "body">
        {error && <Alert variant = "danger">{error}</Alert>}
        {message && <Alert variant = "success">{message}</Alert>}
        <Card>
          <Card.Header as = "h3">Welcome back, {currentUser.displayName}</Card.Header>
          <Card.Body>
            <Card.Title>Access all your account information here.</Card.Title>
            <ListGroup className = "list-group-flush">
              <ListGroupItem>
                <Form onSubmit = {handleEmail}>
                  <Form.Group>
                    <Form.Label>Change your email address</Form.Label>
                    <Form.Control type = "email" placeholder = "Email" ref = {emailRef} />
                    <Form.Text className = "text-muted">We'll never share your email with anyone else.</Form.Text>
                  </Form.Group>
                  <Button disabled = {loading} variant = "primary" type = "submit">
                    Change Email Address
                  </Button>
                </Form>
              </ListGroupItem>
              <ListGroupItem>
                <Form onSubmit = {handlePassword}>
                  <Button disabled = {loading} variant = "primary" type = "submit">
                    Update Password
                  </Button>
                </Form>
              </ListGroupItem>
            </ListGroup>
            <Form onSubmit={handleLogout}><Button disabled={loading} type="submit" variant="danger">Logout</Button></Form>
          </Card.Body>
        </Card>
      </div>
    </div>
  );
}
//What to display if the user is logged out
function NotLoggedIn(){
  let { path, url } = useRouteMatch(); 
  return (
    <div>
      <Nav variant = "pills" defaultActiveKey="/Sign Up">
        <Nav.Item>
          <Nav.Link href = "/Login"><Link style = {{color: 'black', fontSize: ".75em"}} to = {`${url}/Login`}>Already A Member</Link></Nav.Link>
        </Nav.Item>
        <Nav.Item>
          <Nav.Link href = "/Sign Up"><Link style = {{color: 'black', fontSize: ".75em"}} to = {`${url}/Sign Up`}>Sign Up!</Link></Nav.Link>
        </Nav.Item>
        <Nav.Item>
          <Nav.Link href = "/Forgot Password"><Link style = {{color: 'black', fontSize: ".75em"}} to = {`${url}/Forgot Password`}>Forgot Password</Link></Nav.Link>
        </Nav.Item>
        {/* {currentUser ? <Nav.Item><Nav.Link>Hi there</Nav.Link></Nav.Item> : <Nav.Item><Nav.Link>You're not logged in</Nav.Link></Nav.Item>} */}
      </Nav>
      <Switch>
        <Route path = {`${path}/:topicId`}>
          <Topic />
        </Route>
      </Switch>
    </div>
  );
}
//Tells ReactRouter what to display on the screen depending on what was pressed
function Topic(){
    // The <Route> that rendered this component has a
    // path of `/topics/:topicId`. The `:topicId` portion
    // of the URL indicates a placeholder that we can
    // get from `useParams()`.
    let { topicId } = useParams();

    let Display = (topicId === "Login") ? <Login /> : <SignUp />
    if(topicId === "Forgot Password"){
        Display = <ForgotPass />
    }

    return (
      <div style = {{marginLeft:"15%"}}>
          <div style = {{width:"80%", marginTop: "5%"}}>
            {Display}
          </div>
      </div>
    );
  }
//Login portion of the page. 
//Once a user is logged in they will be sent back to the home page
//Creates new user in firebase user database
function Login(){
  const emailRef = useRef(); 
  const passRef = useRef();
  const [error, setError] = useState('');
  const [loading, setLoading] = useState(false)
  const { login } = useAuth(); 
  const history = useHistory();

  async function handleLogin(e){
    e.preventDefault(); 
    try{
      setError("")
      setLoading(true)
      await login(emailRef.current.value, passRef.current.value)
      history.push("/")
    } catch { 
      setError("Failed to log in")
    }
    setLoading(false);
  }

  return (
    <div>
      {error && <Alert variant = "danger">{error}</Alert>}
      <Form onSubmit = {handleLogin}>
        <Form.Group controlId = "formBasicEmail">
          <Form.Label>Email address</Form.Label>
          <Form.Control style = {{width: "20em", margin:"auto"}} type = "email" placeholder = "Email" ref = {emailRef} />
          <Form.Text className = "text-muted">We'll never share your email with anyone else.</Form.Text>
        </Form.Group>
        <Form.Group controlId = "formBasicPassword">
          <Form.Label>Password</Form.Label>
          <Form.Control style={{width: "20em", margin:"auto"}} type = "password" placeholder = "Password" ref = {passRef}/>
        </Form.Group>
        <Button disabled = {loading} variant="primary" type = "submit">Login</Button>
      </Form>
    </div>
  ); 
}
//Signup portion of the page
//User is sent back to homepage once logged in
function SignUp(){
  const fnameRef = useRef(); 
  const lnameRef = useRef(); 
  const emailRef = useRef();
  const passwordRef = useRef(); 
  const passwordconfRef = useRef();
  const [error, setError] = useState('');
  const [loading, setLoading] = useState(false)
  const { signup } = useAuth(); 
  const history = useHistory();

  async function handleSignup(e){
    e.preventDefault()

    if (passwordRef.current.value !== passwordconfRef.current.value){
      return setError('Passwords do not match')
    }
    if (passwordRef.current.value.length < 6){
      return setError('Password must be 6 or more characters long')
    }

    try{
      setError(''); 
      setLoading(true);
      await signup(emailRef.current.value, passwordRef.current.value, fnameRef.current.value, lnameRef.current.value)
      history.push("/")
    } catch {
      setError('Failed to create an Account')
    }
    setLoading(false)
  }

  return (
    <div>
      {error && <Alert variant = "danger">{error}</Alert>}
      <Form onSubmit = {handleSignup}>
        <Form.Group controlId = "formFName" >
          <Form.Label>First Name</Form.Label>
          <Form.Control style = {{width: "20em", margin:"auto"}} type = "text" placeholder = "First name" ref={fnameRef} />
          <Form.Label>Last Name</Form.Label>
          <Form.Control style = {{width: "20em", margin:"auto"}} type = "text" placeholder = "Last name" ref = {lnameRef}/>
        </Form.Group>

        <Form.Group controlId = "formLname">
          <Form.Label>Email address</Form.Label>
          <Form.Control style = {{width: "20em", margin:"auto"}} type = "email" placeholder = "Email" ref = {emailRef} />
          <Form.Text className = "text-muted">We'll never share your email with anyone else.</Form.Text>
        </Form.Group>
        <Form.Group controlId="formBasicPassword">
          <Form.Label>Password</Form.Label>
          <Form.Control style = {{width: "20em", margin:"auto"}} type = "password" placeholder = "Password" ref = {passwordRef} />
          <Form.Text className = "text-muted">Password must be 6 or more characters long</Form.Text>
        </Form.Group>
        <Form.Group controlId = "formBasicPasswordConfirm">
          <Form.Label>Confirm Password</Form.Label>
          <Form.Control style = {{width: "20em", margin:"auto"}} type = "password" placeholder = "Password" ref = {passwordconfRef} />
        </Form.Group>
        <Button disabled = {loading} variant = "primary" type = "submit">Sign Up</Button>
      </Form>
    </div>
  ); 
}

//Displays that the user can enter their email and will receive an email to reset their password
//Displays an error if the user does not have an account
function ForgotPass(){
  const emailRef = useRef();
  const [error, setError] = useState('');
  const [loading, setLoading] = useState(false)
  const { forgot } = useAuth(); 
  const [message, setMessage] = useState('');

  async function handleForgot(e){
    e.preventDefault(); 
    try{
      setError("")
      setLoading(true)
      setMessage("")
      await forgot(emailRef.current.value)
      setMessage("Check your email inbox")
    } catch { 
      setError("Email Not found")
    }
    setLoading(false);
  }

  return (
    <div>
      {error && <Alert variant = "danger">{error}</Alert>}
      {message && <Alert variant = "success">{message}</Alert>}
      <Form style = {{marginTop: "2em"}} onSubmit = {handleForgot}>
        <Form.Group controlId = "forgotPass">
          <Form.Label>Email</Form.Label>
          <Form.Control style = {{width: "20em", margin:"auto"}} type = "email" placeholder = "Email" ref = {emailRef} />
        </Form.Group>
        <Button disabled = {loading} variant = "primary" type = "submit">Send Reset Email</Button>
      </Form> 
    </div>
  );
}