import emailjs from 'emailjs-com'
import { init } from 'emailjs-com'
import './MainStyle.css'
import { useAuth } from '../contexts/AuthContext'
import Button from 'react-bootstrap/Button'
import { React, useState } from 'react'
import Form from 'react-bootstrap/Form'
import { Link } from "react-router-dom"
import Alert from 'react-bootstrap/Alert'
import Card from 'react-bootstrap/Card'
init("user_PJlaGkLc2vNUgxB8J6TVC");

//Determines whether or not the user is logged in
export default function Support(){
	const { currentUser } = useAuth();
	return (
		<div className = "body">
			{currentUser ?  <LoggedIn /> : <NotLoggedIn />} 
		</div>
	);
}
//Set of operations when the user is logged in
function LoggedIn(){
	const { currentUser } = useAuth();
	const [ formSent, setFormSent ] = useState(false)
//Sends the logged in user support form to email
	function sendEmail(e) {
		e.preventDefault();
		setFormSent(true);
		emailjs.sendForm('chaintrackerhelp', 'template_fik2u3y', e.target, 'user_PJlaGkLc2vNUgxB8J6TVC')
		.then((result) => {
			console.log(result.text);
		}, (error) => {
			console.log(error.text);
		});
		e.target.reset()
	}

//Display for logged in user + support info
	return (
		<div className = "center">
			<div className = "body">
			{formSent && <div className = "body"><Alert variant = "success"> Your support form was submitted!</Alert></div>}
				<Card>
					<Card.Header as="h3">Help is on the way, {currentUser.displayName}</Card.Header>
					<Card.Body>
						<Form onSubmit = {sendEmail}>
							<label>
								<input type = "hidden" value = {currentUser.displayName} name = "member_name"/>
								<input type="hidden" value = {currentUser.email} name = "member_email"/>
								<Card.Title>How can we assist you?</Card.Title>
								<Form.Label>Issue</Form.Label>
								<textarea name = "issue" class = "form-control" placeholder = "Description"/>
							</label>
							<br />
							<Button variant = "primary" type = "submit">
								Submit
							</Button>
						</Form>
					</Card.Body>
				</Card>
			</div>
		</div>
	);
}
//Displays navigation bar when user isn't logged in
//Option to either submit a nonmember form or navigate to account page
function NotLoggedIn(){
	return (
		<div className = "center">
			<div className = "body">
				<h1>Need Help?</h1>
				<p>Submit a Support Form and recieve assistance.</p>
				<Link to = {"/Account/SignUp"}>
					<Button>
						Login or Sign Up
					</Button>
				</Link>
			</div>
		</div>
	);	
}